        label="모집 마감일"
        type="date"
        horizontal
      />
      </CCol>
      <CCardHeader style="font-size: 20px; font-weight: 600; border-top: solid 1px; border-color: #CCC; background-color: #EEE;">
        상세 설명
      </CCardHeader>
      <CCardBody>
        <CTextarea
          placeholder="내용을 입력해주세요."
          horizontal
