<script>
export default {
  name: 'NewPost',
  data () {
    return {
      tableItems: {
          title: "캡스톤 디자인 팀 구해요~!",
          sub_title: "스마트 앱 개발 계획인데 같이 하실 분!",
          nickname: "니이익네이임",
          date: "2021-11-23",
          detail: "스마트 앱 개발 관련 캡스톤 디자인 팀 구하고 있습니다!<br>관심있는 분들은 연락주세요~",
          tag: ["#상냥한", "#의기소침한", "#재밌는"],
          head_count_max: 4,
          head_count: 3,
        },
    }
  },
  methods: {
    delay_value(index){
      if (index < 4)
        return index * 100
      else return 0
    },
    color (value, max) {
      let $color
      let $temp = value/max
      if ($temp <= 0.5) {
        $color = 'rgb(37,186,14)'
      } else if ($temp > 0.5 && $temp <= 0.75) {
        $color = 'rgb(255, 127, 0)'
      } else if ($temp > 0.75 && $temp < 0.98) {
        $color = 'rgb(245,89,25)'
      }
      else $color = 'rgb(177,30,49)'
      return $color
    },
    gotoLogin(){
        this.$router.push({ path: '/pages/login' })
    },
  }
}
</script>